package com.jonnyzzz.logo.web.components

import kotlinx.css.*
import kotlinx.css.properties.border
import react.RBuilder
import styled.css
import styled.styledDiv

fun RBuilder.twoColumnsWithBorder(left: RBuilder.() -> Unit, right: RBuilder.() -> Unit) {
  styledDiv {
    css {
      children {
        marginRight = 25.px
        width = 500.px
        height = 500.px
        border(2.px, BorderStyle.solid, Color.brown, 5.px)
        float = Float.left
      }
    }

    left()
    right()
  }

  styledDiv {
    css {
      clear = Clear.both
      height = 25.px
    }
  }
}
