package com.jonnyzzz.logo.web.script

import kotlin.math.PI
import kotlin.math.cos
import kotlin.math.sin


sealed class PenColor {
  object BLACK : PenColor()
}

data class ScriptWorld(
        val sizeX: Int,
        val sizeY: Int,

        val tSize: Int = (sizeX + sizeY) / 75
)

data class ScriptState(
        val x: Int = 0,
        val y: Int = 0,

        val headDirection: Int = 0,

        val penColor : PenColor = PenColor.BLACK,
        val penIsOn: Boolean = true
) {
  val dX get() = sin(2 * PI * headDirection / 360)
  val dY get() = - cos(2 * PI * headDirection / 360)
}

/**
 * Applies world rules (e.g. when it moves too far away)
 * to the proposed script command
 */
fun ScriptWorld.adjustMoves(state: ScriptState, moves: List<ScriptCommand>) : List<ScriptCommand> {
  return moves
}
