package com.jonnyzzz.logo.web.components

import com.jonnyzzz.logo.web.custom
import com.jonnyzzz.logo.web.reducers.ScriptBlock
import com.jonnyzzz.logo.web.reducers.State
import com.jonnyzzz.logo.web.script.ScriptMoveCommand
import com.jonnyzzz.logo.web.script.ScriptState
import com.jonnyzzz.logo.web.script.ScriptWorld
import com.jonnyzzz.logo.web.script.processScript
import react.*
import react.dom.svg
import react.redux.rConnect
import styled.styledSvg

interface SvgWorldPublicProps : RProps {
  var world: ScriptWorld
}

private interface SvgWorldProps : SvgWorldPublicProps, RProps {
  var scripts: List<ScriptBlock>
}

val SvgWorldComponent: RClass<SvgWorldPublicProps> =
        rConnect<State, SvgWorldPublicProps, SvgWorldProps>(
                mapStateToProps = { state, pub ->
                  scripts = state.scripts.filter { it.active }
                  world = pub.world
                }
        ).invoke(SvgWorld::class.js.unsafeCast<RClass<SvgWorldProps>>())

private class SvgWorld(props: SvgWorldProps) : RComponent<SvgWorldProps, RState>(props) {
  override fun RBuilder.render() {
    styledSvg {
      svg {
        attrs["width"] = "500px"
        attrs["height"] = "500px"
        // https://developer.mozilla.org/en-US/docs/Web/SVG/Attribute/viewBox
        // minX, minY, width, height
        attrs["viewBox"] = "" +
                "${-props.world.sizeX/2} " +
                "${-props.world.sizeY/2} " +
                "${props.world.sizeX} " +
                "${props.world.sizeY}"

        var state = ScriptState()
        for (script in props.scripts) {
          for (scriptCommand in processScript(script.script)) {
            val newState = scriptCommand.updateState(state)

            if (scriptCommand is ScriptMoveCommand && state.penIsOn) {
              custom("line") {
                attrs["x1"] = state.x
                attrs["y1"] = state.y
                attrs["x2"] = newState.x
                attrs["y2"] = newState.y
                attrs["strokeWidth"] = if (script.hover) "4px" else "2px"
                attrs["stroke"] = "black"
              }
            }

            state = newState
          }
        }

        turtleHead(props.world, state)
      }
    }
  }
}

private fun RBuilder.turtleHead(world: ScriptWorld, finalState: ScriptState) {
  custom("circle") {
    attrs["cx"] = finalState.x
    attrs["cy"] = finalState.y
    attrs["r"] = world.tSize
    attrs["style"] = object {
      val stroke = "black"
      val fill = "red"
    }
  }

  custom("line") {
    attrs["x1"] = finalState.x
    attrs["y1"] = finalState.y
    attrs["x2"] = finalState.x + world.tSize * finalState.dX
    attrs["y2"] = finalState.y + world.tSize * finalState.dY
    attrs["strokeWidth"] = "2px"
    attrs["stroke"] = "black"
  }
}
