package com.jonnyzzz.logo.web.components

import com.jonnyzzz.logo.web.reducers.ScriptAddAction
import kotlinx.css.*
import kotlinx.html.js.onClickFunction
import org.w3c.dom.HTMLTextAreaElement
import react.*
import react.dom.defaultValue
import react.dom.form
import react.redux.rConnect
import redux.WrapperAction
import styled.css
import styled.styledButton
import styled.styledDiv
import styled.styledTextArea

interface ScriptAddFormPublicProps : RProps {
  var defaultText: String?
}

private interface ScriptAddFormInnerProps : RProps, ScriptAddFormPublicProps {
  var onSubmit: (String) -> Unit
}

val ScriptAddFormComponent: RClass<ScriptAddFormPublicProps> =
        rConnect<ScriptAddAction, WrapperAction, ScriptAddFormPublicProps, ScriptAddFormInnerProps>(
                mapDispatchToProps = { dispatch, _ ->
                  onSubmit = { dispatch(ScriptAddAction(it)) }
                }
        ).invoke(ScriptAddForm::class.js.unsafeCast<RClass<ScriptAddFormInnerProps>>())

private class ScriptAddForm(props: ScriptAddFormInnerProps) : RComponent<ScriptAddFormInnerProps, RState>(props) {
  private val textAreaRef = createRef<HTMLTextAreaElement>()

  override fun RBuilder.render() {
    form {
      styledDiv {
        css {
          verticalAlign = VerticalAlign.bottom
          children {
            float = Float.left
            marginRight = 25.px
          }
        }

        styledTextArea {
          attrs.readonly = false
          attrs.rows = "5"
          attrs.cols = "80"
          attrs.defaultValue = props.defaultText ?: ""
          ref = textAreaRef
          css {
            jetBrainsMono()
          }
        }

        styledButton {
          css {
            height = 50.px
            width = 150.px
          }

          attrs.onClickFunction = {
            it.preventDefault()
            props.onSubmit(textAreaRef.current.value)
            textAreaRef.current.value = props.defaultText ?: ""
          }

          +"Add"
        }
      }
    }
  }
}
