package com.jonnyzzz.logo.web.script

sealed class ScriptCommand {
  abstract fun updateState(state: ScriptState) : ScriptState
}

data class ScriptMoveCommand(val size: Int) : ScriptCommand() {
  override fun updateState(state: ScriptState): ScriptState {
    val pX = size * state.dX
    val pY = size * state.dY
    return state.copy(x = state.x + pX.toInt(), y = state.y + pY.toInt())
  }
}

data class ScriptTurnCommand(val angle: Int) : ScriptCommand() {
  override fun updateState(state: ScriptState): ScriptState {
    var newAngle = (state.headDirection + angle) % 360
    if (newAngle < 0) newAngle += 360
    return state.copy(headDirection = newAngle)
  }
}

object ScriptJumpHome : ScriptCommand() {
  override fun updateState(state: ScriptState): ScriptState {
    return state.copy(x = 0, y = 0, headDirection = 0)
  }
}

object ScriptPenUpCommand : ScriptCommand() {
  override fun updateState(state: ScriptState): ScriptState {
    return state.copy(penIsOn = false)
  }
}

object ScriptPenDownCommand : ScriptCommand() {
  override fun updateState(state: ScriptState): ScriptState {
    return state.copy(penIsOn = true)
  }
}
